<template>
  <div>
      <div class="bond-list">
        <div class="bond-item" v-for="(bono, index) in bonos" :key="index">
            <h4>{{bono.nombre}}</h4>
            <h5>{{bono.ruc}}</h5>
            <div class="data-row"><span>Valor nominal:</span>        <span class="mon">{{bono.valor_nominal.toFixed(2)}}</span></div>
            <div class="data-row"><span>Precio del Bono:</span>      <span class="mon">{{bono.precio_actual.toFixed(2)}}</span></div>
            <div class="data-row"><span>Retorno de Bono:</span>      <span class="mon">{{bono.valor_actual.toFixed(2)}}</span></div>
            <br>
            <div class="data-row"><span>Tipo de bono:</span>         <span>{{tipoBono(bono)}}</span></div>
            <br>
            <div class="data-row"><span>Pago por cupon:</span>       <span class="mon">{{bono.cupon.toFixed(2)}}</span></div>
            <div class="data-row"><span>Duración del Bono:</span>    <span >{{bono.periodo}} <small>años</small></span></div>
            <div class="data-row"><span>Días entre pagos:</span>     <span>{{bono.frecuencia}} <small>días</small></span></div>
            <div class="data-row"><span>Cantidad de pagos:</span>    <span>{{bono.cxp}} <small>pagos</small></span></div>
            <div class="end"></div>
            <div class="data-row" v-if="bono.TIR">
                <span>TIR:</span>
                <span class>{{(100 * bono.TIR).toFixed(4)}}%</span>
            </div>
            <div class="data-row">
                <span>B/C:</span>
                <span class>{{(100 * bono.valor_actual / bono.precio_actual).toFixed(6)}}%</span>
            </div>
            <div class="data-row">
                <span>VNA:</span>
                <span class="mon">{{bono.vna.toFixed(2)}}</span>
            </div>
        </div>
      </div>
  </div>
</template>

<script>
export default {
    data: ()=>({
        bonos : []
    }),
    methods:{
        tipoBono(bono){
            switch (Math.sign(bono.valor_nominal - bono.precio_actual) + 1) {
                case 0:     return 'bono con prima';
                case 1:     return 'bono emitido a la par';
                case 2:     return 'bono emitido a descuento';
            }
        }
    },
    computed:{
    },
    mounted(){
        this.bonos = this.$store.state.aux_bonos;
    }
}
</script>

<style>
.end{
    flex: 1;
}
.mon::before{
    content: 'S/. ';
    font-size: 75%;
}
.bond-item > h4{
    margin: 1.5rem 0;
    margin-bottom: .5rem;
}
.bond-item > h5{
    margin: 0;
    margin-bottom : .5rem;
    height: 1rem;
}
br + .data-row{
    height: 36px;
}
.data-row > span:first-child{
    text-align: left;
}
.data-row > span{
    display: inline-block;
    font-size: 11px;
    margin-top: auto;
}
.data-row > span:last-child{
    margin-left: auto;
    font-size: 15px;
    text-align: right;
}
.end ~ .data-row{
    background-color: #acf;
}
.end ~ .data-row> span{
    font-weight: 600;
}
.data-row:last-child{
    flex-direction: column;
}
.data-row:last-child > span:first-child{
    text-align: center;
    font-size: 100%;
    font-weight: 600;
}

.data-row:last-child > span:last-child{
    box-shadow:         inset 0px 1px 8px 1px #0006; margin:0; margin-bottom: 1rem;
    padding: 8px 16px; background-color: #cfc; border-radius: 4px; font-weight: 700;
    display: block;
    font-size: 15px;
}

.bond-list{
    display: flex;
    flex-direction: row;
    justify-content: center;
}
.bond-list > .bond-item{
    max-width: 220px;
    min-height: 480px;

    display: flex;
    flex-direction: column;
    flex: 1;
    border:solid;
    border-left: 0;
}
.bond-list > .bond-item:first-child{
    border-left: solid;
}
.bond-list > .bond-item > div{
    display: flex;
    padding: 0 1rem;
}
</style>